import * as React from 'react';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import './radio.scss'

export default function ControlledRadio(props) {


  return (
    <FormControl component="fieldset">
      <RadioGroup
      className={props.className ? props.className : 'custom-radio'}
        aria-label="gender"
        name="controlled-radio-buttons-group"
        value={props.radioValue}
        onChange={props.handleChangeRadio}
        sx={{
            color: '#04724d',
            '&.Mui-checked': {
              color: '#04724d',
            },
          }}
      >
       {
           props.radioOptions.map(item=> <FormControlLabel value={item} control={<Radio />} label={item} />)
       }
      </RadioGroup>
    </FormControl>
  );
}