import React from "react";
import "./mcqOptionsCard.scss";
import OptionComponent from "./OptionComponent";
import AddIcon from "@mui/icons-material/Add";

class McqOptionsCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="mcq-options-card-main">
        <div className="options-heading-div">
          <div className="options-heading">Options</div>
        </div>

        <div className="options-body">
          {this.props.options && this.props.options.map((item) => {
            return (
              <div style={{ marginBottom: "10px" }}>
                <OptionComponent
                  checked={item.correct}
                  optionValue={item.text}
                  onChangeOptionValue={(e) => {
                    this.props.handleInput(e, item, this.props.questionDetail);
                  }}
                  handleChange={() => {
                    this.props.handleCheckbox(item, this.props.questionDetail);
                  }}
                  deleteHandler={() => {
                    this.props.handleDeleteItem(
                      item,
                      this.props.questionDetail
                    );
                  }}
                />
              </div>
            );
          })}

          <div className="add-option">
            <div
              className="add-option-circle"
              onClick={() => {
                this.props.handleAddOption(this.props.questionDetail);
              }}
            >
              <AddIcon
                style={{ height: "20px", width: "20px", color: "#fff" }}
              />
            </div>
            <div className="add-option-text">Add Option</div>
          </div>
        </div>
      </div>
    );
  }
}

export default McqOptionsCard;
