import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import React from "react";
import CancelIcon from '@material-ui/icons/Cancel';
import './dialog.scss'

export default function ControlledDialog(props) {

    return  <Dialog
          className={props.dialogClassName}
          open={props.showDialog}
          onClose={props.handleCloseDialog}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          {/* <DialogTitle
            className="alert-dialog-title"
          
          >
            {props.DialogTitle}
          </DialogTitle> */}
             {props.showClose && (
           <div className="top-wrapper"> 
           <div className="icon-heading">
               <div className={props.iconTopClass}>
                   {props.iconTop}
               </div>
               <div className={props.headingTopClass}>
                   {props.headingTop}
               </div>

           </div>
                <div className="close" onClick={props.handleCloseDialog}>
            <CancelIcon  style={{height:'25px',width:'25px',color:'#bdbdbd'}}/>
            </div>
           </div>
          )}

          <div className={props.dialogBodyClass ? props.dialogBodyClass : 'dialog-body' }>
       
          {props.children}
    

          </div>

       <div style={{display:'flex',justifyContent:'flex-end'}}>
       {
            props.dialogAction1Text  ?   <DialogActions>
            <div onClick={props.handleAction1} className={props.dialogAction1Class}>
              {props.dialogAction1Text}
            </div> 
            </DialogActions> : null

        }

        {
   props.dialogAction2Text ? <DialogActions>
            <div onClick={props.handleAction2} className={props.dialogAction2Class}>
            {props.dialogAction2Text}
            </div>
          </DialogActions> : null
        }
       </div>
        </Dialog>


}