import React, { useEffect, useState, Fragment } from "react";
import Card from "@material-ui/core/Card";
import "./MCQ.scss";
import CancelIcon from "@material-ui/icons/Cancel";
import Button from "../../components/Controls/Button/Button";
import McqQuestionCard from "../../components/AssessmentComponents/McqQuestionCard";
import CreateMCQ from "../../components/AssessmentComponents/CreateMCQ";
import * as AssessmentActions from "../../store/Assessment/AssessmentAction";
import Autocomplete from "../../components/Atoms/AutoComplete";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { secondsToMinutes } from "../../utils/utility";
import PreviewStepper from "../../components/AssessmentComponents/PreviewStepper";
import { Chip } from "@material-ui/core";
// import LoadingBar from "react-top-loading-bar";
import InfiniteScroll from "react-infinite-scroll-component";
import ReactGA from "react-ga";

const MCQ = (props) => {
  const [isAddQuestion, setAddQuestion] = useState(false);
  const [activeStepNew, setactiveStepNew] = useState(1);
  const [searchVal, setSearchVal] = useState([]);
  const [questionsList, setQuestionsList] = useState([]);
  const [rowId, setRow] = useState({});
  const [isEdit, setIsEdit] = useState(false);
  const [progress, setProgress] = useState(0);
  const [stop, setSTop] = useState(false);
  const { questionType } = props.AssessmentReducer;
  const {
    questions,
    isQuestionDeleted,
    isQuestionAdded,
    isQuestionUpdated,
    isGetQuestions,
  } = props.AssessmentReducer;
  const totalPages = questions.data && questions.data.totalPages;
  let optionsValue = [];
  let listOfQuestions = (questions.data && questions.data.results) || [];
  const page = questions.data && questions.data.page;
  const [deleteIndex, setDeleteIndex] = useState(0);
  const [deleteId, setDeleteId] = useState("");
  const showAddQuestions = () => {
    setAddQuestion(true);
    setIsEdit(false);
    setRow({});
  };
  const hideAddQuestion = (value) => {
    setAddQuestion(value);
  };
  useEffect(() => {
    ReactGA.pageview(window.location.pathname);
    const payload = { name: "Mcq" };
    setProgress(70);
    props.AssessmentAction.getQuestionTypes(payload);
  }, []);

  useEffect(() => {
    if (isQuestionAdded) {
      hideAddQuestion(false);
      const payload = {
        page: 1,
        limit: 10,
        type: questionType.data.results[0] && questionType.data.results[0].id,
      };
      props.AssessmentAction.getQuestions(payload);
      setQuestionsList([]);
    }
  }, [isQuestionAdded]);
  const deleteQuestion = (item, index) => {
    /**delete question */
    setProgress(70);
    setDeleteIndex(index);
    setDeleteId(item.id);
    const payload = {
      id: item.id,
    };
    props.AssessmentAction.deleteQuestion(payload);
  };
  useEffect(() => {
    if (isQuestionDeleted) {
      const filteredDta = questionsList.filter((x) => x.id !== deleteId);

      // const payload = {
      //   page: questionsList.length,
      //   limit: 1,
      //   type: questionType.data.results[0] && questionType.data.results[0].id,
      // };
      // props.AssessmentAction.getQuestions(payload);
      setQuestionsList(filteredDta);
      // setQuestionsList([])
      setRow({});
    }
  }, [isQuestionDeleted]);
  useEffect(() => {
    if (listOfQuestions.length > 0) {
      setProgress(100);
      setQuestionsList([...questionsList, ...listOfQuestions]);
    }
    if (searchVal.length > 0) {
      setQuestionsList(listOfQuestions);
    }
  }, [listOfQuestions]);
  const handleSearch = (e, value) => {
    setSearchVal(value);
    if (value.length === 0) {
      setQuestionsList([]);
    }
  };
  const clearFilter = () => {
    setSearchVal([]);
    setQuestionsList([]);
  };
  const editQuestion = (item) => {
    setRow(item);
    setAddQuestion(true);
    setIsEdit(true);
  };
  const renderValues2 = (value, getTagProps) => {
    optionsValue = value;
    let newVals = [];
    value.map((val) => {
      let i = val.split(",");
      i.length === 1 ? newVals.push(i[0]) : i.map((el) => newVals.push(el));
    });
    return newVals.map((option, index) => (
      <Chip variant='outlined' label={option} {...getTagProps({ index })} />
    ));
  };
  const onKeyDown = () => {};
  useEffect(() => {
    if (searchVal.length > 0) {
      setProgress(70);
      const payload = {
        page: 1,
        limit: 10,
        type:
          questionType.data &&
          questionType.data.results[0] &&
          questionType.data.results[0].id,
        searchTerm: searchVal.toString(),
      };
      props.AssessmentAction.getQuestions(payload);
    } else {
      if (
        questionType.data &&
        questionType.data.results[0] &&
        questionType.data.results[0].id
      ) {
        setProgress(70);
        const payload = {
          page: 1,
          limit: 10,
          type: questionType.data.results[0] && questionType.data.results[0].id,
        };
        props.AssessmentAction.getQuestions(payload);
      }
    }
  }, [searchVal, questionType]);
  useEffect(() => {
    if (isQuestionUpdated) {
      const payload = {
        page: 1,
        limit: 10,
        type: questionType.data.results[0] && questionType.data.results[0].id,
      };
      props.AssessmentAction.getQuestions(payload);
      setQuestionsList([]);
      hideAddQuestion(false);
    }
  }, [isQuestionUpdated]);
  const fetchMoreData = async () => {
    const { questionType } = props.AssessmentReducer;
    await setactiveStepNew(activeStepNew + 1);
    if (!(activeStepNew + 1 > totalPages)) {
      const payload = {
        page: activeStepNew + 1,
        limit: 10,
        type:
          questionType.data &&
          questionType.data.results[0] &&
          questionType.data.results[0].id,
      };
      props.AssessmentAction.getQuestions(payload);
    }
  };

  return (
    <Fragment>
      <div className='resume-card'>
        <Card className='mcq-wrapper'>
          {isAddQuestion ? (
            <CreateMCQ
              isFromQuestionTab={true}
              skillData={[]}
              defaultEnable={true}
              isEdit={isEdit}
              rowData={rowId}
              hideAddQuestion={(data) => hideAddQuestion(data)}
              allQuestions={listOfQuestions}
            />
          ) : (
            <div className='list-card-main'>
              <div style={{ display: "flex", justifyContent: "flex-end" }}>
                <div className='filter-by-skils'>Clear Filter</div>
                <div className='close' onClick={clearFilter}>
                  <CancelIcon
                    style={{ height: "25px", width: "25px", color: "#bdbdbd" }}
                  />
                </div>
              </div>

              <div className='row mb'>
                <Button
                  width='19%'
                  height='34px'
                  borderRadius='4px'
                  className='res-btn'
                  onClick={() => showAddQuestions()}
                >
                  Add Questions
                </Button>
                <div className='searchInputWrap'>
                  <Autocomplete
                    placeholder=''
                    multiple
                    className='multiSelectContainer questionSkills'
                    onKeyDown={onKeyDown}
                    onChange={handleSearch}
                    value={searchVal}
                    renderTags={(value, getTagProps) =>
                      renderValues2(value, getTagProps)
                    }
                    options={
                      (props.skillData &&
                        props.skillData.map((x, i) => {
                          return {
                            value: i,
                            label: x,
                          };
                        })) ||
                      []
                    }
                  />
                </div>
              </div>
              <InfiniteScroll
                dataLength={questionsList.length}
                next={activeStepNew > totalPages ? {} : fetchMoreData}
                hasMore={activeStepNew > totalPages ? false : true}
                endMessage={<p style={{ textAlign: "center" }}>No More Data</p>}
                loader={
                  activeStepNew > totalPages ? (
                    <p></p>
                  ) : (
                    <button
                      className='load-more-btn'
                      onClick={() => fetchMoreData()}
                    >
                      {" "}
                      Load more
                    </button>
                  )
                }
              >
                {questionsList.map((_mcq, index) => (
                  <div style={{ marginBottom: "15px" }}>
                    <McqQuestionCard
                      publish={_mcq.isPublished}
                      mcqText={_mcq.text}
                      time={secondsToMinutes(_mcq.suggestedDuration)}
                      skills={_mcq.skills}
                      mcqOptions={_mcq.metadata.option || []}
                      deleteHandler={() => {
                        setRow(_mcq);
                        deleteQuestion(_mcq);
                      }}
                      editHandler={() => editQuestion(_mcq)}
                    />
                  </div>
                ))}
              </InfiniteScroll>
            </div>
          )}
        </Card>
      </div>
    </Fragment>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    AssessmentAction: bindActionCreators(AssessmentActions, dispatch),
  };
};
const mapStateToProps = (state) => {
  return {
    AssessmentReducer: state.AssessmentReducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(MCQ);
