import React, { useState, useEffect } from "react";
import "./mcqQuestionCard.scss";
import "./listCardVerbal.scss";
import DeleteIcon from "@material-ui/icons/Delete";
import CheckIcon from "@material-ui/icons/Check";
import ControlledDialog from "../../components/Atoms/Dialog";
import * as AssessmentActions from "../../store/Assessment/AssessmentAction";
import CreateIcon from "@mui/icons-material/Create";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import PercentIcon from "../../assets/images/Assessment/skills.svg";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));

function McqQuestionCard(props) {
  const [dialog, setDialog] = useState(false);
  // const [text,setText]=useState(props.mcqText || '')
  const { isQuestionDeleted, isTestUpdated } = props.AssessmentReducer;
  useEffect(() => {
    if (isTestUpdated) {
      setDialog(false);
    }
  }, [isTestUpdated]);
  useEffect(() => {
    if (isQuestionDeleted) {
      setDialog(false);
    }
  }, [isQuestionDeleted]);
  return (
    <div
      className="mcq-question-card-main"
      onClick={props.onClick}
      style={{
        backgroundColor: props.selected
          ? "#04724d"
          : props.publish
          ? "#fff"
          : props.backgroundColor
          ? props.backgroundColor
          : "#fff",
        color: props.selected ? "#fff" : "#333",
        display: "flex",
        flexDirection: props.coding ? "row" : "column",
        justifyContent: props.coding ? "space-between" : "",
        cursor: props.onClick ? "pointer" : "",
      }}
    >
      <div className="mcq-question-wrapper">
        <div className="text-options">
          {/* <div className="mcq-text">{props.mcqText}</div> */}
          <div dangerouslySetInnerHTML={{ __html: props.mcqText }}></div>
          <div className="mcq-options">
            {props.mcqOptions &&
              props.mcqOptions.map((option, index) => {
                return (
                  <div className="option">
                    <div className="sno"> {index + 1}.</div>
                    <div className="option-text">{option.text} </div>
                    {option.correct ? (
                      <div style={{ marginLeft: "10px" }}>
                        {" "}
                        <CheckIcon
                          style={{
                            color: props.selected ? "#fff" : "#04724d",
                            height: "20px",
                            width: "20px",
                          }}
                        />{" "}
                      </div>
                    ) : null}
                  </div>
                );
              })}
          </div>
        </div>
      </div>
      <div
        className="mcq-time-delete"
        style={{ marginTop: props.coding ? "0px" : "-20px" }}
      >
        <div className="mcq-time">{props.time}</div>
        {!props.hideSkills && !props.isViewOnly && (
          <div className="list-verbal-delete">
            <LightTooltip
              title={
                <div class="list-tooltip-main">
                  <div class="skills-heading"> Skill Set </div>
                  <div class="main-skills">
                    {props.skills &&
                      props.skills.map((item) => {
                        return <div class="chip-div"> {item}</div>;
                      })}
                  </div>
                </div>
              }
              placement="top"
            >
              <img
                src={PercentIcon}
                style={{ height: "15px", width: "15px" }}
                alt=""
              />
            </LightTooltip>
          </div>
        )}
        {props.publish || props.hideEdit ? null : (
          <div className="list-verbal-delete" onClick={props.editHandler}>
            <CreateIcon
              style={{ height: "15px", width: "15px", color: "#1473e6" }}
            />
          </div>
        )}

        {!props.publish && props.deleteHandler  ? (
          <div className="list-verbal-delete" onClick={() => setDialog(true)}>
            <DeleteIcon
              style={{ height: "15px", width: "15px", color: "red" }}
            />
          </div>
        ) : null}
      </div>
      <ControlledDialog
        showDialog={dialog}
        handleCloseDialog={() => {
          setDialog(false);
        }}
        dialogClassName={"deleteDialog"}
        dialogAction1Text="Cancel"
        dialogAction2Text="Confirm"
        showClose={true}
        iconTop={
          <DeleteIcon style={{ height: "15px", width: "15px", color: "red" }} />
        }
        headingTop="Delete"
        iconTopClass="icon-top"
        headingTopClass="heading-top"
        dialogAction1Class="dialog-action-1-class"
        dialogAction2Class="dialog-action-2-class"
        handleAction1={() => {
          setDialog(false);
        }}
        handleAction2={props.deleteHandler}
      >
        <div className="dialog-main-heading">
          Are you sure you want to delete this Question?
        </div>
        <div className="dialog-sub-heading">
          Click to confirm to delete this question permanently
        </div>
      </ControlledDialog>
    </div>
  );
}
const mapDispatchToProps = (dispatch) => {
  return {
    AssessmentAction: bindActionCreators(AssessmentActions, dispatch),
  };
};
const mapStateToProps = (state) => {
  return {
    AssessmentReducer: state.AssessmentReducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(McqQuestionCard);
