import React from "react";
import './previewStepper.scss';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';


export default function PreviewStepper(props) {

    return <div className="previewStepper-main">
        <div className="preview-steps">{props.activeStep}/{props.totalStep}</div>
        <div className="preview-arrow-buttons">
            <div className={!props.disabled1 ? 'arrow-div' : 'arrow-div-disabled'} onClick={!props.disabled1 ? props.backArrowHandler : null}>
                <ArrowBackIosNewIcon  />
            </div>
            <div className={!props.disabled2 ? 'arrow-div' : 'arrow-div-disabled'}  onClick={!props.disabled2 ? props.forwardArrowHandler : null}>
                <ArrowForwardIosIcon />
            </div>
        </div>

    </div>

}