import React from "react";
import './optionComponent.scss';
import ControlledCheckbox from "../Atoms/Checkbox";
import { TextField } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import ClickAwayListener from "@material-ui/core/ClickAwayListener";



export default function OptionComponent(props) {
    const [open, setOpen] = React.useState(true);
    // const [focus, setFocus] = React.useState(false);

    // window.onkeydown = function(e) {
    //     if (e.which == 9) {
    //         setFocus(true)
        
    //     }
    // };
   

    return <div className="option-component-main">
        <div className="checkbox-input-div">
      <div className="input-1">  <ControlledCheckbox checked={props.checked}
             size={20}
             borderRadius={2}
             borderColor={"#04724d"}
              label={''} 
              onChange={props.handleChange} /></div>

<ClickAwayListener onClickAway={()=>{
    if(props.optionValue){
        setOpen(false)
    }  }}>
        <div className="input-2">
    <TextField id="standard-basic"  variant="standard"

    placeholder="Enter Option"
      className="option-input" value={props.optionValue} onChange={props.onChangeOptionValue}/>
    </div> 
{/* {
    open==true ? <div className="input-2">
    <TextField id="standard-basic"  variant="standard"
    placeholder="Enter Option"
      className="option-input" value={props.optionValue} onChange={props.onChangeOptionValue}/>
    </div> 
    : <div onClick={()=>{setOpen(true)}} className="optionValue">{props.optionValue}</div>
} */}
</ClickAwayListener>
        </div>

        <div className="option-delete" onClick={props.deleteHandler}>
                <DeleteIcon style={{height:'12px',width:'12px',color:'red'}}  />
                </div>
        
       
     

    </div>

}